import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Code from '../models';

export default {
  name: 'code',

  match(node: JsonML): boolean {
    return getTagName(node) === 'code';
  },

  convert(node: JsonML): Block {
    return Code.create({
      data: {
        syntax: 'plaintext',
        theme: 'default',
        ...(getAttributes(node) || {}),
      },
    });
  },
};
